<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <div class="fix-basic-table">
      <div class="tab-button-div">
        <a-form
          :model="searchInfo"
          name="searchInfo"
          layout="inline"
          autocomplete="off"
          class="search-form"
          ref="searchFormRef"
        >
          <a-form-item
            label="时间纬度"
            name="dateCycle"
            :rules="[{ required: true, max: 30 }]"
            :labelCol="{ span: 15 }"
            style="width: 26.5%"
          >
            <a-select
              v-model:value="searchInfo.dateCycle"
              allow-clear
              :options="dateCycleArr"
              style="width: 100%"
            />
          </a-form-item>

          <a-form-item
            name="yearValue"
            label="年度"
            :labelCol="{ span: 6 }"
            :rules="[{ required: searchInfo.dateCycle == 'year', message: '请选择年度' }]"
            v-show="searchInfo.dateCycle == 'year'"
            style="width: 26.5%"
          >
            <a-date-picker
              v-model:value="searchInfo.yearValue"
              value-format="YYYY"
              style="width: 100%"
              picker="year"
            />
          </a-form-item>

          <a-form-item
            name="startMonth"
            label="月份"
            :labelCol="{ span: 6 }"
            :rules="[{ required: searchInfo.dateCycle == 'month', message: '请选择月份' }]"
            v-show="searchInfo.dateCycle == 'month'"
            style="width: 26.5%"
          >
            <a-range-picker
              v-model:value="searchTimeInfo.searchMonth"
              format="YYYY-MM"
              value-format="YYYY-MM"
              style="width: 100%"
              picker="month"
              @change="handleDateChange"
            />
          </a-form-item>

          <a-form-item
            name="stationName"
            label="电站"
            :labelCol="{ span: 6 }"
            :rules="[{ required: false, message: '请选择电站' }]"
            style="width: 26.5%"
          >
            <a-select
              v-model:value="searchInfo.stationName"
              show-search
              allow-clear
              mode="multiple"
              style="width: 100%"
              placeholder="请选择电站"
              :options="stationOptions"
              @change="handleChange"
              labelInValue
              max-tag-count="responsive"
            />
          </a-form-item>
          <a-form-item v-if="expand" style="width: 100%; height: 10px" />
          <a-form-item
            name="ognName"
            label="所属公司"
            :labelCol="{ span: 6 }"
            :rules="[{ required: false, message: '请选择所属公司' }]"
            style="width: 26.5%"
            v-if="useUserStore().isMultiOrganization && expand"
          >
            <a-select
              v-model:value="searchInfo.ognName"
              show-search
              allow-clear
              style="width: 100%"
              placeholder="请选择所属公司"
              :options="ognOptions"
              @change="handleChangeOgn"
            />
          </a-form-item>

          <a-form-item class="tab-button">
            <a-button style="margin: 0 8px" @click="resetFrom">重置</a-button>
            <a-button type="primary" class="tab-button" @click="searchList">查询</a-button>
            <a
              v-if="useUserStore().isMultiOrganization"
              style="margin-left: 12px; font-size: 14px"
              @click="expandSearch"
            >
              <template v-if="expand">
                收起
                <UpOutlined />
              </template>
              <template v-else>
                展开
                <DownOutlined />
              </template>
            </a>
          </a-form-item>
        </a-form>
      </div>
      <div class="tab-table-div">
        <BasicTable @register="registerTable" @resize-column="(w, col) => (col.width = w)"
          ><template #toolbar>
            <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExportIndex"
              >导出</a-button
            >
          </template></BasicTable
        >
      </div>
    </div>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { BasicTable, useTable } from '@/components/Table';
  import { columns, exportExcel } from './data';
  import { onMounted, reactive, ref, unref } from 'vue';
  import { defHttp } from '/@/utils/http/axios';
  import { SelectProps } from 'ant-design-vue';
  import { PageWrapper } from '/@/components/Page';
  import { useRouter } from 'vue-router';
  import { DownOutlined, UpOutlined } from '@ant-design/icons-vue';
  import { useI18n } from '@/hooks/web/useI18n';
  import { exportExcelFile } from '@/utils/file/download';
  import { IconEnum } from '/@/enums/appEnum';
  import { useMessage } from '@/hooks/web/useMessage';
  import { useUserStore } from '/@/store/modules/user';

  const { t } = useI18n();
  const expand = ref(false);
  const stationOptions = ref<SelectProps['options']>([]) as any;
  const ognOptions = ref<SelectProps['options']>([]) as any;

  enum Api {
    getTrainingStatisticsUrl = '/bi/trainingStatistics/trainingStatisticsDetail',
    LIST_STATION_URL = '/bi/common/station/info',
    LIST_OGN_URL = '/bi/common/org/info',
  }

  let dateCycleArr = ref<SelectProps['options']>([
    { label: '年份', value: 'year' },
    { label: '月份', value: 'month' },
  ]);
  const searchTimeInfo = reactive<any>({
    searchMonth: null,
    searchDate: null,
  }); // 查询信息

  const searchInfo = reactive<any>({
    ognId: null,
    stationId: null,
    dateCycle: 'year',
    yearValue: new Date().getFullYear() + '',
    startMonth: null,
    endMonth: null,
    startDate: null,
    endDate: null,
  }); // 查询信息

  const getTrainingStatisticsData = async () => {
    let areaList = await getTrainingStatisticsDataList(searchInfo);
    if (searchInfo.stationId != undefined) {
      searchInfo.stationId = searchInfo.stationId.split('|');
      searchInfo.stationName = searchInfo.stationName.split('|');
    }

    return areaList;
  };

  const getTrainingStatisticsDataList = (params) =>
    defHttp.get(
      {
        url: Api.getTrainingStatisticsUrl,
        params,
      },
      { isOnlyResult: true },
    );

  const getStation = (params) =>
    defHttp.get(
      {
        url: Api.LIST_STATION_URL,
        params,
      },
      {},
    );

  const getOgn = (params) =>
    defHttp.get(
      {
        url: Api.LIST_OGN_URL,
        params,
      },
      {},
    );

  const { createConfirm, createMessage } = useMessage();
  // 导出
  function handleExportIndex() {
    createConfirm({
      title: t('common.message.confirmTitle'),
      iconType: 'warning',
      content: t('common.message.exportMessage'),
      async onOk() {
        const query = searchInfo;
        query.filename = `培训统计.xls`;
        await exportExcel(query).then((res) => {
          exportExcelFile(res?.data, query.filename);
        });
        query.filename = null;
        createMessage.success(t('common.exportSuccessText'));
      },
    });
  }

  function handleChange(value: string, option) {
    if (value.length == 0) {
      searchInfo.stationId = undefined;
      searchInfo.stationName = undefined;
    }
  }

  function handleChangeOgn(value: string, option) {
    searchInfo.ognName = option.value;
    searchInfo.ognId = option.lable;
  }

  function initStationOptions() {
    getStation({}).then((res) => {
      if (res.result && res.result.length > 0) {
        res.result.forEach((item) => {
          stationOptions.value.push({ value: item.stationName, lable: item.id });
        });
      }
    });

    getOgn({}).then((res) => {
      if (res.result && res.result.length > 0) {
        res.result.forEach((item) => {
          ognOptions.value.push({ value: item.label, lable: item.value });
        });
      }
    });
  }

  //-----------------跳转相关--------------------
  const router = useRouter();
  const { currentRoute } = router;
  const route = unref(currentRoute);
  const routeParam = route.query;
  // const dealJumpParam = async () => {
  //   searchInfo.dateCycle = routeParam.dateType;
  //   searchTimeInfo.searchMonth = [routeParam.startDate, routeParam.endDate];
  //   handleDateChange();
  // };

  onMounted(async () => {
    initStationOptions();
    if (routeParam.startDate) {
      searchInfo.dateCycle = routeParam.dateType;
      searchTimeInfo.searchMonth = [routeParam.startDate, routeParam.endDate];
      handleDateChange();
      //针对穿透赋值
      if (routeParam.selectType == 'station') {
        searchInfo.stationName = routeParam.name;
        searchInfo.stationId = routeParam.id;
      }
      if (routeParam.selectType == 'company') {
        searchInfo.ognName = routeParam.name;
        searchInfo.ognId = routeParam.id;
      }

      if (
        routeParam.stationId != undefined &&
        routeParam.stationId != null &&
        routeParam.stationId != ''
      ) {
        searchInfo.stationId = routeParam.stationId.split('|');
      }

      if (routeParam.stationName != undefined && routeParam.stationName != null) {
        searchInfo.stationName = routeParam.stationName.split('|');
      }
    }
  });
  function expandSearch() {
    redoHeight();
    expand.value = !expand.value;
  }
  const [registerTable, { reload, redoHeight }] = useTable({
    title: '培训统计',
    api: getTrainingStatisticsData,
    columns: columns,
    useSearchForm: false,
    bordered: true,
    pagination: false,
    showIndexColumn: false,
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    afterFetch: function (data) {
      const count = {};
      for (let index = 0; index < data.length; index++) {
        const element = data[index];
        if (count[element.stationOrganId]) {
          count[element.stationOrganId]++;
          continue;
        }
        count[element.stationOrganId] = 1;
      }
      for (let index = 0; index < data.length; index++) {
        const element = data[index];
        for (const key in count) {
          if (element.stationOrganId === key) {
            element.rowOrgSpan = count[key];
            count[key] = 0;
          }
        }
      }
      return data;
    },
  });

  function resetFrom() {
    searchInfo.ognId = null;
    searchInfo.stationId = undefined;
    searchInfo.ognName = null;
    searchInfo.stationName = undefined;
    searchInfo.dateCycle = 'year';
    searchInfo.yearValue = new Date().getFullYear() + '';
    searchInfo.startMonth = null;
    searchInfo.endMonth = null;
    searchInfo.startDate = null;
    searchInfo.endDate = null;
    searchTimeInfo.searchDate = null;
    searchTimeInfo.searchMonth = null;

    reload();
  }

  // 查询
  const searchFormRef = ref();
  function searchList() {
    let stationNames = [];
    let stationIdIds = [];
    if (searchFormRef.value) {
      searchFormRef.value.validateFields().then(() => {
        if (Array.isArray(searchInfo.stationName)) {
          searchInfo.stationName.forEach((item) => {
            if (item.option != undefined) {
              stationNames.push(item.option.value);
              stationIdIds.push(item.option.lable);
            }
          });

          if (stationNames.length > 0) {
            let stn = stationNames.join('|');
            let sti = stationIdIds.join('|');
            searchInfo.stationId = sti;
            searchInfo.stationName = stn;
          } else {
            let stn = searchInfo.stationName.join('|');
            let sti = searchInfo.stationId.join('|');
            searchInfo.stationId = sti;
            searchInfo.stationName = stn;
          }
        }

        if (searchTimeInfo.searchMonth != null) {
          searchInfo.startMonth = searchTimeInfo.searchMonth[0];
          searchInfo.endMonth = searchTimeInfo.searchMonth[1];
        }

        if (searchTimeInfo.searchDate != null) {
          searchInfo.startDate = searchTimeInfo.searchDate[0];
          searchInfo.endDate = searchTimeInfo.searchDate[1];
        }

        reload();
      });
    }
  }

  const handleDateChange = () => {
    searchInfo.startMonth = null;
    searchInfo.endMonth = null;
    searchInfo.startDate = null;
    searchInfo.endDate = null;

    if (searchTimeInfo.searchMonth != null) {
      searchInfo.startMonth = searchTimeInfo.searchMonth[0];
      searchInfo.endMonth = searchTimeInfo.searchMonth[1];
    }

    if (searchTimeInfo.searchDate != null) {
      searchInfo.startDate = searchTimeInfo.searchDate[0];
      searchInfo.endDate = searchTimeInfo.searchDate[1];
    }
  };
</script>
<style lang="less" scoped>
  .tab-model {
    margin-top: 10px;
    margin-right: 10px;
    margin-left: 10px;
  }

  .tab-button-div {
    padding-top: 12px;
    padding-bottom: 12px;

    :deep(.ant-form-item-label) {
      width: 120px;
      max-width: 120px;
      text-align-last: right;
    }
  }

  .tab-table-div {
    height: calc(100vh - 86px);
    margin-top: 10px;
    overflow: hidden;

    :deep(.ant-table-placeholder) {
      height: 591px;
    }
  }

  .tab-button {
    margin-right: 5px;

    &:last-child {
      margin-right: 12px;
    }
  }

  .search-form {
    position: relative;

    .search-btns {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
    }
  }

  .fix-basic-table {
    padding: 10px;

    :deep(&.vben-basic-table .ant-table-wrapper) {
      padding: 10px 16px 12px;
    }

    :deep(.table-settings .anticon-setting) {
      margin-right: 0;
    }
  }
</style>
